import api from '../http'
// 获取所有角色列表
export const getAllRoleList = () => {
	return api.http({
		url:'/user/role/list/all',
		method:'get'
	})
}

export const findRoleListForPage = (params) => {
	return api.http({
		url:`/user/role/list/page`,
		method:'get',
		params
	})
}

export const deleteRoleById = (id) => {
	return api.http({
		url:`/user/role/delete/id/${id}`,
		method:'delete',
	})
}

export const insertRole = (role) => {
	return  api.http({
		url:`/user/role/insert`,
		method:'put',
		data:role
	})
}

export const updateRole = (role) => {
	return  api.http({
		url:`/user/role/update`,
		method:'put',
		data:role
	})
}

export const findRoleById = (id) => {
	return  api.http({
		url:`/user/role/find/id/${id}`,
		method:'get',
	})
}