import api from '../http'

export const getOrderListForPage = (queryForm) => {
	return api.http({
		url:'/shop/order/list/page',
		method:'get',
		params:queryForm
	})
}


export const findOrderById = (id) => {
	return api.http({
		url:`/shop/order/find/id/${id}`,
		method:'get',
		data:id
	})
}


export const deleteOrderById = (id) => {
	return api.http({
		url:`/shop/order/delete/id/${id}`,
		method:'delete',
	})
}

export const sendGoods = (params) => {
	return api.http({
		url:`/shop/order/send`,
		method:'get',
		params
	})
}

export const fallbackGoodsOver = (id) => {
	return api.http({
		url:`/shop/order/fallback/goods/over`,
		method:'get',
		params:{id}
	})
}
