import api from '../http'
import QS from 'qs'
export const getDeptList = () => {
	return api.http({
		url:'/user/dept/list/all',
		method:'get'
	})
}


export const getDeptListForPage = (queryForm) => {
	return api.http({
		url:'/user/dept/list/page',
		method:'get',
		params:queryForm
	})
}

export const insertDept = (addForm) => {
	return api.http({
		url:'/user/dept/insert',
		method:'put',
		data:addForm
	})
}

export const findDeptById = (id) => {
	return api.http({
		url:`/user/dept/find/id/${id}`,
		method:'get',
		data:id
	})
}

export const updateDept = (addForm) => {
	return api.http({
		url:`/user/dept/update`,
		method:'put',
		data:addForm
	})
}

export const deleteDeptById = (id) => {
	return api.http({
		url:`/user/dept/delete/id/${id}`,
		method:'delete',
	})
}

export const getDeptListUser = () => {
	return api.http({
		url:'/user/dept/list/user',
		method:'get'
	})
}
export const addDeptUser = (params) => {
	return api.http({
		url:'/user/dept/add/user',
		method:'put',
		headers:{
			"Content-Type":"application/x-www-form-urlencoded"
		},
		data:QS.stringify(params,{ indices: false })
	})
}

export const deleteDeptUserById = (id) => {
	return api.http({
		url:'/user/dept/delete/user',
		method:'delete',
		params:{id}
	})
}