import api from '../http'

export const getGoodsListForPage = (queryForm) => {
	return api.http({
		url:'/shop/goods/list/page',
		method:'get',
		params:queryForm
	})
}

export const getGoodsListAll = () => {
	return api.http({
		url:'/shop/goods/list/all',
		method:'get'
	})
}

export const insertGoods = (addForm) => {
	return api.http({
		url:'/shop/goods/insert',
		method:'put',
		data:addForm
	})
}

export const findGoodsById = (id) => {
	return api.http({
		url:`/shop/goods/find/id/${id}`,
		method:'get',
		data:id
	})
}

export const updateGoods = (addForm) => {
	return api.http({
		url:`/shop/goods/update`,
		method:'put',
		data:addForm
	})
}

export const deleteGoodsById = (id) => {
	return api.http({
		url:`/shop/goods/delete/id/${id}`,
		method:'delete',
	})
}

export const setGoodsIsOnSale = (params) => {
	return api.http({
		url:`/shop/goods/set/onsale`,
		method:'get',
		params:params
	})
}